import { css } from 'react-emotion';

const cardInputStyle = css`
  font-weight: bold;
  font-family: inherit;
  font-size: 1em;
  border: none;
  background: transparent;
  outline: none;
  border-bottom: solid 1px gray;
`;

export default cardInputStyle;



